* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.container [data-start] {
  text-align: center;
  color: #f0f8ff;
  background-color: #7e7a7ab3;
  border-radius: 6px;
  padding: 20px 40px;
  font-size: 18px;
}

.container [data-start]:disabled {
  opacity: .5;
}

.container [data-stop] {
  text-align: center;
  color: #f0f8ff;
  background-color: #7e7a7ab3;
  border-radius: 6px;
  padding: 20px 40px;
  font-size: 18px;
}

.container [data-stop]:disabled {
  opacity: .5;
}

.timer {
  gap: 20px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  font-size: 20px;
  font-weight: 300;
  display: flex;
}

.field .value {
  font-size: 40px;
}

.timer__container {
  width: 350px;
  margin: 0 auto;
  padding: 0 auto;
  font-family: Ubuntu;
}

#datetime-picker {
  width: 200px;
  border: 1px solid #68d197;
  border-radius: 6px;
  padding: 10px 0;
}

[data-start] {
  text-align: center;
  color: #f0f8ff;
  background-color: #57d290b3;
  border-radius: 6px;
  padding: 10px 20px;
  font-size: 18px;
}

[data-start]:disabled {
  opacity: .5;
}

